@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=EB+Garamond&display=swap");

body {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}
nav {
  color: #272343;
  font-family: "Poppins", "Arial Narrow", Arial, sans-serif;
  font-style: normal;
}
.div-bg {
  background-color: #ffaf7a;
}
li .nav-link {
  font-size: 16px;
  color: white;
}
li :hover {
  color: #ffaf7a;
}
a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active {
  text-decoration: none;
}
.navbar-color {
  background-color: black;
  margin-bottom: 0;
}
.welcome-message {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  line-height: 2.5rem;
  font-weight: 200;
  font-size: 20px;
}
#logo {
  left: 0rem;
  background-color: orangered;
  border-radius: 50%;
}
#hero {
  background-image: linear-gradient(
      to right,
      rgba(255, 255, 0, 0.3),
      rgba(255, 255, 0, 0, 6),
      rgba(255, 255, 0, 0.6)
    ),
    url("./img/road.webp");
  background-size: cover;
  background-repeat: no-repeat;
  padding-bottom: 70vh;
  position: relative;
  margin-right: 0px;
}

.header-img {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  height: 90vh;
  width: 100%;
  margin-right: 0;
}
.top-right {
  position: absolute;
  top: 16rem;
  left: 5rem;
}
.paragraph-heading {
  font-family: "Big Shoulders Display", sans-serif;
  line-height: 2.5rem;
  font-weight: 500;
  font-size: 23px;
  border: 3px solid #ff4500;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.paragraph {
  font-family: "Poppins", sans-serif;
  line-height: 2.5rem;
  font-weight: 500;
  font-size: 32px;
}
.side-icon {
  color: #ff4500;
}
::placeholder {
  font-size: 16px;
}
form .input {
  border-color: #ff9d5c;
}

form .input :focus {
  border-color: #ff9d5c;
}
form input .form-control {
  border-color: #ff9d5c;
}

form {
  border-radius: 5px;
  font-size: 20px;
  font-family: "Poppins";
  font-weight: 400;
  border: 2px solid #ff4500;
  box-shadow: 5px 5px 20px #ff9d5c;
  color: white;
}
.form-btn {
  background-color: red;
  border: 1px solid red;
}

.flex-container {
  display: flex;
  flex-direction: row;
}
.header-text1 {
  position: relative;
}
.header-text {
  position: absolute;
  font-size: 30px;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  top: 10%;
  color: white;
}
.header-txt {
  position: absolute;
  font-size: 45px;
  font-family: "Big Shoulders Display", sans-serif;
  top: 10%;
  color: #ff4500;
}
.loader {
  position: fixed;
  left: 50%;
  top: 50%;
  z-index: 1000;
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #ff4500;
  border-right: 16px solid #ff9d5c;
  border-bottom: 16px solid #ff4500;
  border-left: 16px solid #ffaf7a;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}
.heading {
  font-size: 30px;
  font-family: sans-serif;
  font-weight: 700;
  color: #ff4500;
  text-transform: uppercase;
  word-spacing: 3pt;
  letter-spacing: 0.5pt;
  text-align: center;
}
.welcomeImg {
  height: 150px;
  width: 150px;
}

footer {
  padding-top: 4vh;
  background-color: #ff4500;
  color: black;
  font-size: 2.5vh;
  font-family: "Roboto", "sans-serif";
  position: relative;
  bottom: 0;
  width: 100%;
}
.list {
  color: black;
  font-size: 18px;
  font-family: "Roboto", sans-serif;
}
.warranty {
  height: 480px;
  width: 640px;
}
.form-phone {
  display: none;
}
.btn-primary {
  color: black;
  background-color: red;
  border: 3px solid red;
  font-size: 18px;
  width: 13rem;
  height: 3rem;
  text-shadow: none;
  margin: 3px;
}

hr {
  background-color: #f3f3f3;
}
.card-title {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: #ff4500;
}
.card-text {
  color: white;
}
#menu {
  right: 2%;
  top: 25%;
  position: fixed;
  z-index: 1;
}

#menu1 {
  right: 2%;
  top: 34%;
  position: fixed;
  z-index: 1;
}

#bottomNav {
  display: none;
}
.batteryProd {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.battery {
  text-align: center;
  height: 150px;
  font-family: "Roboto", sans-serif;
  font-size: 40px;
  text-shadow: #4286f4;
  color: #47484c;
  text-decoration: underline;
}
.glyph {
  font-size: 40px;
}

.text-danger {
  font-size: 12px;
}
.blog-heading {
  font-size: 2rem;
  font-family: "Big Shoulders Display";
  color: #ff4500;
  word-spacing: 3pt;
  letter-spacing: 0.5pt;
}
.blog-list-heading {
  font-size: 1.6rem;
  font-family: "Big Shoulders Display";
  margin-top: 3vw;
  color: #ff4500;
}
.blog-paragraph-heading {
  font-size: 1.5rem;
  font-family: "Roboto";
  margin-top: 2vw;
  color: #ff4500;
}
.blog-img {
  height: 32rem;
  width: 100%;
}
.list-group-item {
  border-top: 2px solid #ff4500;
}
.haks {
  height: 40rem;
  width: 60rem;
}
.autob {
  height: 9rem;
  width: 9rem;
}

.card {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px #47484c;
  margin-top: 2rem;
  justify-content: center;
  width: 100%;
  margin-bottom: 2rem;
}
.postCard {
  box-shadow: 5px 5px 10px #47484c;
  border-bottom-left-radius: 50px;
  height: 50rem;
}
.card-img-top {
  width: 100%;
  height: 18rem;
}
.brandCars {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.brandImg {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 60px;
  width: 79px;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
  margin-right: 5px;
  border: 1px solid #ff4500;
}
.AutoPartbrandImg {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 90px;
  width: 90px;
  margin-top: 1px;
  margin-bottom: 1px;
  margin-left: 1px;
  margin-right: 1px;
  border: 1px solid #47484c;

}
.brandCol {
  grid-row-start: 1;
  grid-row-end: 3;
}
.bottomNav {
  display: none;
}
.item {
  margin-right: 5px;
  margin-left: 5px;
}

.item-img {
  height: 30vh;
  width: 100%;
}
.welcome-expressive {
  font-size: 28px;
}
.social-icons-container {
  display: flex;
  flex: auto;
  justify-content: center;
}
.social-icons {
  color: #ff4500;
  justify-content: center;
  font-size: 50px;
  margin: 1rem;
  padding: 1rem;
  box-shadow: 5px 5px 20px #ff9d5c;
}
.social-icons :hover {
  background-color: #ff9d5c;
  box-shadow: 5px 5px 20px #ff9d5c;
  transition: background 0.2s;
  border-radius: 3px;
}
.header-whatsapp {
  color: white;
  border: 3px solid #25d366;
}
.header-whatsapp :hover {
  background-color: #25d366;
  transition: 0.3s;
}
#myForm {
  margin: 1rem;
  padding: 1rem;
}
.welcome-paragraph {
  font-size: large;
  font-family: "Roboto", sans-serif;
}
.vert .carousel-inner .carousel-item {
  height: 90px;
}

.vert .carousel-inner {
  color: #ff4500;
  justify-content: center;
  font-size: 32px;
  margin: 1rem;
  padding: 1rem;
  box-shadow: 5px 5px 20px #ff9d5c;
  font-family: "Poppins",sans-serif;
  font-weight: 500;
  width: 100%;
}
.auto_parts_button {
  display: none;
}
.header-paragraph-carousel {
  padding-top: 7rem;
  padding-left: 5rem;
}

@media (max-width: 992px) {
  .form-phone {
    align-items: center;
    display: inline-block;
  }
  .formtab {
    display: none;
  }
  .auto_parts_button {
    display: none;
  }
}
@media (min-width: 768px) {
  .header-img {
    height: 75vh;
    width: 100%;
  }
  .top-right {
    position: absolute;
    top: 19rem;
    left: 1rem;
  }
  .top-left {
    display: none;
  }
  .nav1 {
    font-size: 15px;
  }
  .heading {
    font-size: 28px;
  }
  .form-phone {
    display: none;
  }

  #botNav {
    display: none;
  }
  #navMob {
    display: none;
  }
  .paragraph {
    font-weight: 300;
    font-size: 16px;
  }
  .batteryProd {
    height: 60vh;
    width: 60vh;
  }
  .battery {
    font-size: 25px;
  }
  .glyph {
    font-size: 35px;
  }
  footer {
    position: relative;
    bottom: 0;
    left: 0;
    bottom: 0;
    width: 100%;
  }
  .card {
    margin-left: 0;
  }
  .blog-paragraph-heading {
    font-size: 1.7rem;
  }
  .welcomeImg {
    height: 80px;
    width: 80px;
  }
  .header-car-img {
    padding-top: 1rem;
    display: flex;
    flex-direction: column;
  }
  .vert .carousel-inner .carousel-item {
    padding: 1rem;
    width: 150px;
  }
  .vert .carousel-inner {
    width: 210px;
    height: 90px;
    font-size: 16px;
    font-weight: 500;
  }
  .auto_parts_button {
    display: none;
  }
  .header-paragraph-carousel {
    padding-left: 5rem;
  }
}
@media (max-width: 576px) {
  .header-img {
    height: 75vh;
    margin: 0;
  }
  .header-car-img {
    padding-top: 1rem;
    margin-left: -2rem;
    height: 12em;
    display: flex;
    flex-direction: column;
  }
  .top-right {
    position: relative;
    top: -5rem;
    left: 0.1rem;
  }
  .top-left {
    display: none;
  }
  .form-phone {
    background: url("./img/whatsappbg.jpg");
    display: inline-block;
  }
  .paragraph {
    font-size: 16px;
  }
  .list {
    font-size: 15px;
  }
  ::placeholder {
    font-size: 16px;
  }
  .small {
    font-size: 15px;
  }
  .nav1 {
    font-size: 20px;
  }
  .heading {
    font-size: 23px;
  }
  .header-text {
    font-size: 16px;
  }
  .btn-primary {
    font-size: 14px;
    width: 10rem;
    height: 2.6rem;
    text-shadow: none;
    margin: 3px;
  }
  .flex-container {
    flex-direction: column;
    align-items: center;
  }
  .fontSize {
    font-size: 12px;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  }
  #menu {
    display: none;
  }
  #menu1 {
    display: none;
  }
  .fixed-bottom {
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1030;
    background-color: #ff4500;
  }
  .bottomNav {
    display: block;
    color: white;
  }
  .bottomNav :hover {
    color: #ff9d5c;
  }
  .bottomNav :active {
    color: #ff9d5c;
  }
  .batteryProd {
    height: 50vh;
    width: 50vh;
  }
  footer {
    position: relative;
    padding-bottom: 150px;
    left: 0;
    bottom: 0;
    width: 100%;
  }
  .battery {
    font-size: 20px;
  }
  .glyph {
    font-size: 30px;
  }
  .blog-img {
    height: 175px;
    width: 100%;
  }
  .card {
    margin-left: 0;
  }
  .blog-paragraph-heading {
    font-size: 1.4rem;
  }
  .welcomeImg {
    height: 50px;
    width: 50px;
  }
  .vertical-center {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
  }
  .mobile-bottom-navbar {
    font-size: 16px;
    font-family: "EB Garamond", sans-serif;
    margin: 1em;
  }
  .header-paragraph-carousel {
    position: relative;
    padding-top: 4rem;
    padding-left: 1rem;
  }
  .vert .carousel-inner .carousel-item{
    padding: none;
    margin: none;
    width: 150px;
    height: 90px;
  }
  .vert .carousel-inner {
    width: 100%;
    height: 90px;
    font-size: 16px;
    font-weight: 500;
  }
  .auto_parts_button {
    display: block;
    right: 2%;
  top: 5%;
  position: fixed;
  z-index: 1;
  }
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes mymove {
  from {
    left: 0;
  }
  to {
    left: 200px;
  }
}
